<template>
    <v-container class="fill-height" fluid>
        <v-row class="align-center justify-center">
            <v-col cols="12" sm="8" md="6" lg="4">
                <v-alert
                    v-model="alert"
                    border="left"
                    close-text="Close Alert"
                    :type="alertType"
                    transition="scale-transition"
                    prominent
                >
                    {{ alertMessage }}
                </v-alert>
                <v-fade-transition>
                    <v-card v-show="!loading" class="elevation-12 rounded-lg">
                        <v-toolbar :color="getAppColors.primary" dark>
                            <v-avatar size="50px">
                                <v-img
                                    alt="Logo"
                                    src="@/assets/logo-only.png"
                                    contain
                                />
                            </v-avatar>
                            <v-toolbar-title dark>
                                core<strong>CLEAR</strong> Portal
                            </v-toolbar-title>
                        </v-toolbar>
                        <v-card-title>Forgot Password</v-card-title>
                        <v-card-text>
                            <v-form ref="form" v-model="valid">
                                <v-text-field
                                    prepend-icon="person"
                                    :color="getAppColors.primary"
                                    label="Username or Email"
                                    name="usernameOrEmail"
                                    type="text"
                                    autocomplete="usernameOrEmail"
                                    v-model="usernameOrEmail"
                                    :rules="usernameOrEmailRules"
                                    @keydown.enter="submit"
                                    required
                                />
                            </v-form>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn text rounded large to="/">Cancel</v-btn>
                            <v-spacer />
                            <v-btn
                                :color="getAppColors.primary"
                                class="white--text"
                                :disabled="!valid"
                                :loading="loading"
                                @click="submit"
                                rounded
                                large
                            >
                                Reset password
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-fade-transition>
                <v-dialog
                    v-model="loading"
                    hide-overlay
                    persistent
                    transition="dialog-bottom-transition"
                    width="300"
                >
                    <v-card :color="getAppColors.primary" dark>
                        <v-card-text>
                            Please wait...
                            <v-progress-linear
                                indeterminate
                                color="white"
                                class="mb-0"
                            ></v-progress-linear>
                        </v-card-text>
                    </v-card>
                </v-dialog>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';

export default {
    name: 'ForgotPassword',
    data() {
        return {
            loading: false,
            valid: false,
            alert: false,
            alertMessage: '',
            alertType: 'success',
            usernameOrEmail: '',
            usernameOrEmailRules: [
                (v) => !!v || 'Username or Email is required',
            ],
        };
    },
    computed: {
        ...mapGetters(['getAppColors', 'getInternalLoginController']),
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        submit() {
            if (this.$refs.form.validate()) {
                this.loading = true;
                this.alert = false;
                this.doResetPasswordNotify();
            }
        },
        async doResetPasswordNotify() {
            let formData = new FormData();
            formData.append('username_or_email', this.usernameOrEmail);

            try {
                let response = await axios.post(
                    `${
                        this.getInternalLoginController +
                        '/do_reset_password_notify/1'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );

                this.loading = false;

                if (response.data.success === true) {
                    this.openAlert('success', response.data.message);
                } else {
                    this.openAlert('error', response.data.message);
                }
            } catch (error) {
                console.error(error);
            }
        },
    },
};
</script>
